
import { Component, Vue,Emit,Watch,PropSync,Prop } from 'vue-property-decorator';
import { Form } from "element-ui";
import {range_list,notice_list,alarm_type,cycle_num,cycle_time,event_type,event_name,productList}from '../../assets/alarm_data';
import Service from '../../https/alarm/list'
import {deal_list} from '../../utils/transIndex';
@Component({})
export default class RuleConfig extends Vue{
    @Prop({default:()=>[]})strategy_data!:any
    private tab_key:string="0"
    private edit_key:string="";
    private selected_product_id:String="";
    private edit_rule_id:string=""
    private index_list:any=[]
    private rule_data:any={
        name:'',
        metricID:[],
        event_type:'',
        event_name:'',
        alram_type:'',
        notice:[],
        level:[{
            range:'>=',
            num:'',
            cycle_time:'1',
            cycle_num:'1',
            alram_type:'2',
            notice:["email"],
            metricUnit:''
        }]
    }
    private static_list={
        range_list,
        notice_list,
        alarm_type,
        cycle_num,
        cycle_time,
        event_type,
        event_name:[],
    }
    private product_list:any=[]
    private selected_products:any = []
    created() {
        this.getProductList()
    }
    private valid(name){
        this.$nextTick(()=>{
            const form = this.$refs[name] as Form
            form && form[0].validate(async (valid)=>{

            })
        })

    }
    private get event(){
        const {event_type,event_name,alram_type,notice}=this.rule_data
        return {
            event_type,
            event_name,
            alram_type,
            notice,
        }
    }
    @Watch('event',{immediate:true,deep:true})
    private watch_event(newVal){
        this.valid('event_form')
    }
    private async getProductList(){
        let res:any = await Service.get_product_list({})
        if(res.code==='Success'){
            res.data.forEach(item=>{
                this.product_list.push({...item,visible:false,rule_list:[]})
            })
        }
    }
    // @Watch("rule_data.metricID")
    private watch_metricID(newVal,list){
        this.valid('rules_form')
        let unit:string="";
        list.forEach(item=>{
            item.children.forEach(inn=>{
                if(inn.value===newVal[1]){
                    unit = inn.unit
                }
            })
        })
        this.rule_data.level.map(item=>item.metricUnit = unit)
    }
    // @Watch("rule_data.event_type")
    private watch_event_type(nv,list,pro_id){
        let key_list=['event_name_zh','event_id']
        let label_list=['title','id']
        let fil:any = list.filter((item:any)=>item.event_type===nv);

        this.selected_products.map(item=>{
            if(item.id === pro_id){
                item.event_name = fil.length>0 ? deal_list(fil[0].list,label_list,key_list) : [];
                const ids = item.event_name.map(ev=>ev.id)//事件列表所有的id
                if((new Set([...ids,this.rule_data.event_name])).size>ids.length){//如果属于当前这个事件名称里，长度最大为ids的长度
                    this.rule_data.event_name=''
                }
            }
        })

    }
    private setEventList(data){
        let key_list=['event_name_zh','event_id']
        let label_list=['title','id']
        let event_type = data
        //为了渲染初始化时已有规则的列表描述
        let event_name=[]
        data.map(item=>{
            event_name=[...event_name,...item.list]
        })
        event_name=deal_list(event_name,label_list,key_list)
        return [event_type,event_name]
    }

    private get_index_list_by_value(value,index_list){
        let arr = []
        index_list.forEach(item=>{
            item.children.forEach(inn=>{
                if(inn.value===value){
                    arr=[item.value,value]
                }
            })
        })
        return arr;
    }
    //转化指标项列表
    private setIndexList(data){
        let index_list:any=[]
        data && data.forEach(element => {
            let temp=[]
            element.metric_infos.forEach(item=>{
                temp.push({
                    value:item.metric,
                    label:item.description,
                    unit:item.unit,
                    description:item.description,
                })
            })
            index_list.push({
                value:element.name,
                label:element.name,
                children:temp
            })
        });
        return index_list
    }
    private setRules(element,list,index_list,event_list){
        const obj = {//相当于rule_data
                id:element.id,
                tab_key:element.ruleRecords[0].alarmType==="event" ? '1' : '0',
                name:element.name,
                metricID:element.ruleRecords[0].alarmType==="event" ? [] : this.get_index_list_by_value(element.ruleRecords[0].metricID,index_list),
                event_type:element.ruleRecords[0].eventType,
                event_name:element.ruleRecords[0].eventId && element.ruleRecords[0].eventId.length>0 ? element.ruleRecords[0].eventId[0] : '',
                metricUnit:element.ruleRecords[0].alarmType==="event" ? '' : element.ruleRecords[0].metricUnit,
                alram_type:element.ruleRecords[0].alarmType==="event" ? element.ruleRecords[0].level.toString() : '',
                notice:element.ruleRecords[0].alarmType==="event" ? element.ruleRecords[0].alarmMethod : [],
                level:list,
                desc:element.ruleRecords[0].alarmType==="event" ?
                [`${this.trans_arr(element.ruleRecords[0].level.toString(),this.static_list.alarm_type)} | ${this.trans_arr(element.ruleRecords[0].eventId,event_list[1])}`]

                    :
                    list.map(inn=>{
                    return `${this.getDescription(element.ruleRecords[0].metricID,index_list)} ${inn.range} ${inn.num}${inn.metricUnit} ${this.trans_arr(inn.alram_type,this.static_list.alarm_type)} 持续${inn.cycle_num}次就报警`
                })
            }
            const fil:any = this.selected_products.filter(item=>item.id===element.productType)
            if(fil.length>0){
                this.selected_products.map(item=>{
                    if(item.id===element.productType){
                        item.rule_list = [...item.rule_list,obj];
                        item.index_list = index_list;
                        item.event_type = event_list[0];
                        item.event_name = event_list[1]
                    }
                    return item;
                })
            }else{
                this.selected_products.push({
                    id : element.productType,
                    name : element.productName,
                    visible : false,
                    rule_list : [obj],
                    index_list : index_list,
                    event_type : event_list[0],
                    event_name : event_list[1]
                })
            }
    }
    //编辑或者选中某个已有的规则
    @Watch("strategy_data",{immediate:true,deep:true})
    private watch_strategy_data(newVal,ov){
        if(Object.keys(newVal).length===0){
            return;
        }
        this.selected_products=[]
        newVal.rules.forEach(async element=>{
            const list=[]
            element.ruleRecords.forEach(item=>{
                list.push({
                    range:item.metricCondition,
                    num:item.metricValue,
                    cycle_time:item.metricPeriod.toString(),
                    cycle_num:item.metricPeriodNum.toString(),
                    alram_type:item.alarmType==="event" ? '' : item.level.toString(),
                    notice:item.alarmType==="event" ? [] : item.alarmMethod,
                    metricUnit:item.metricUnit
                })
            })
            Promise.all([
                Service.get_index_list({type:element.productType}),
                Service.get_event_list({type:element.productType})
            ]).then((res:any)=>{
                this.setRules(element,list,this.setIndexList(res[0].data),this.setEventList(res[1].data))
            })
        })
    }
    private getDescription(id,list){
        let name=""
        list.forEach(item=>{
            item.children.forEach(inn=>{
                if(inn.value===id){
                    name = inn.description
                }
            })
        })
        return name
    }
    private handle(obj){
        this.add_product(obj)
    }
    private add_product(e){
        if(this.selected_products.filter(item=>item.id===e).length>0){
            return;
        }
        Promise.all([
            Service.get_index_list({type:e}),
            Service.get_event_list({type:e})
        ]).then((res:any)=>{
            const fil = this.product_list.filter(item=>item.id===e)
            fil[0].rule_list = [];
            fil[0].index_list = this.setIndexList(res[0].data);
            fil[0].event_type = this.setEventList(res[1].data)[0];
            fil[0].event_name = this.setEventList(res[1].data)[1];
            this.selected_products=[...this.selected_products,...fil ];
        })

    }
    private del_product(id:String){
        this.selected_products = this.selected_products.filter(item=>item.id!==id)
        if(this.selected_products.length===0){
            this.selected_product_id=""
        }
    }
    private show_rule_box(id:string="",val:String="add"){//todo表单未清空
        this.selected_products.map(item=>{
            if(item.id===id){
                item.visible = !item.visible;
                if(this.edit_key==="" && item.visible){//新增规则打开弹窗
                    this.clear()
                }
                if(val==="edit" && !item.visible){
                    this.edit_key=""
                }

            }else{
                item.visible = false
            }
            return item;
        })
    }
    private add_level(){
        let len:number = this.rule_data.level.length
        const obj={
            range:'>=',
            num:'',
            cycle_time:'1',
            cycle_num:'1',
            alram_type:'2',
            notice:["email"],
            metricUnit:this.rule_data.level[0].metricUnit
        }
        this.rule_data = {...this.rule_data,level:[...this.rule_data.level,obj]}
    }
    private del_level(){
        const {level}=this.rule_data
        this.rule_data = {...this.rule_data,level:level.filter((item,index)=>index<level.length-1)}
    }
    private add_rule(){//确定添加规则或者编辑规则
        const form = this.tab_key==="0" ? this.$refs.rules_form[0] as Form : this.$refs.event_form[0] as Form;
        form.validate(async (valid)=>{
            if(valid){
                if(this.tab_key==="0" && this.rule_data.level.some(item=>item.num==="")){
                    return;
                }
                const {selected_products,tab_key,edit_key,edit_rule_id} = this
                selected_products.map(item=>{
                    if(item.visible){
                        if(edit_key===""){//新增
                            const obj = {...this.rule_data,tab_key,id:item.id+Math.floor((Math.random()*9+1)*1000000)}
                            if(tab_key==="0"){//阈值报警
                                item.rule_list=[...item.rule_list,{
                                    ...obj,
                                    desc:this.rule_data.level.map(inn=>{
                                        return `${this.getDescription(this.rule_data.metricID[1],item.index_list)} ${inn.range} ${inn.num}${inn.metricUnit} ${this.trans_arr(inn.alram_type,this.static_list.alarm_type)} 持续${inn.cycle_num}次就报警`
                                    })
                                }]
                            }else{//事件报警
                                item.rule_list=[...item.rule_list,{
                                    ...obj,
                                    desc:[`${this.trans_arr(this.rule_data.alram_type,this.static_list.alarm_type)} | ${this.trans_arr(this.rule_data.event_name,item.event_name)}`]
                                }]
                            }
                        }else{
                            item.rule_list = item.rule_list.map(inn => {
                                if(inn.id === edit_rule_id){
                                    inn = {...inn,...this.rule_data}
                                    inn.desc = inn.tab_key==="0" ? inn.level.map(inner=>{
                                        return `${this.getDescription(this.rule_data.metricID[1],item.index_list)} ${inner.range} ${inner.num}${inner.metricUnit}${this.trans_arr(inner.alram_type,this.static_list.alarm_type)} 持续${inner.cycle_num}次就报警`
                                    }) : [`${this.trans_arr(inn.alram_type,this.static_list.alarm_type)} | ${this.trans_arr(inn.event_name,item.event_name)}`]
                                }
                                return inn;
                            })
                        }


                    }
                    return item;
                })

                this.edit_key=""
                this.edit_rule_id=""
                this.show_rule_box()

            }
        })
    }

    private cancel(){
        this.edit_key=""
        this.show_rule_box()

    }
    private del_rule(pro_id:string,id:string){
        this.selected_products.map(item=>{
            if(item.id===pro_id){
                item.rule_list = item.rule_list.filter(inn=>inn.id!==id)
            }
            return item;
        })
    }
    private clear(){
        this.tab_key="0";
        this.$nextTick(()=>{
            const rules_form:any = this.$refs.rules_form[0] as Form
            const event_form:any = this.$refs.event_form[0] as Form
            rules_form.resetFields()
            event_form.resetFields()
            this.valid('rules_form');
            this.valid('event_form')
            this.rule_data={
                name:'',
                metricID:[],
                event_type:'',
                event_name:'',
                alram_type:'',
                notice:[],
                level:[{
                    range:'>=',
                    num:'',
                    cycle_time:'1',
                    cycle_num:'1',
                    alram_type:'2',
                    notice:["email"],
                    metricUnit:''
                }]
            }
            // this.rule_data.name=''
            // this.rule_data.level=[
            //     {
            //         range:'>=',
            //         num:'',
            //         cycle_time:'1',
            //         cycle_num:'1',
            //         alram_type:'2',
            //         notice:["email"],
            //         metricUnit:''
            //     }
            // ]
        })

    }
    private edit_rules(pro_id:string,id:string,key:string){
        this.tab_key = key
        this.edit_key = key
        this.edit_rule_id=id
        this.show_rule_box(pro_id,'edit')
        const fil:any = this.selected_products.filter(item=>item.id === pro_id)
        const fil_rule:any = fil[0].rule_list.filter(item=>item.id===id)
        this.rule_data = JSON.parse(JSON.stringify(fil_rule[0]))
        if(fil_rule[0].tab_key==='1'){
            this.watch_event_type(fil_rule[0].event_type,fil[0].event_type,fil[0].id)

        }

    }
    private change_rule_notice(val){
    }
    private changeTab(val){
    }
    private trans_arr(id,arr){
        if(typeof id==="string"){
            const fil = arr.filter(item=>item.id===id)
            return fil[0].title
        }else{
            let fil = []
            arr.map(item=>{
                if(id.indexOf(item.id)>-1){
                    fil.push(item.title)
                }
            })
            return fil
        }


    }
}
